.Calendarbody{
    height: 78vh;
    width: 95%;
    background-color: #212121;
    border-radius: 0.5em;
    color: #fff;

    display: flex;
    align-items: center;
    justify-content: center;
}
.Calendarbody table{
    text-align: center;
    width: 100%;
    height: 100%;
    border-radius: 0.5em;
}
.Calendarbody tr{
    width: 2em;
}
.Calendarbody td{
    height: 1.5em;
    width: 2em;
}
.titleCalendar{
    height: 40px;
    background-color: rgba(63,191, 91, 0.3);
    border-radius: 0.5em 0.5em 0 0 ;
}
.titleCalendar .titleText{
    font-size: 1.5em;
}
.weekContent{
    background-color: #fff;
    color: #212121;
    font-weight: bold;
    font-size: 0.6em;
    padding: 0.1em;
}
.CalendarContent{
    background-color: #fff;
    color: #212121;
    font-weight: 600;
}
.footerCalendar{
    font-size: 0.9em;
    padding: 0;
    background-color: rgba(63,191, 91, 0.3);
    border-radius: 0em 0em 1em 1em;
}
.tdNote{
    position: relative;
}

.round{
    position: absolute;
    bottom: 0.85em;
    left: 0.75em;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red; 
    width: 1.5em;
    height: 1.5em;
    border-radius:999em;
    margin:0.5em auto 0 auto;
    
    
}
